.article-wrapper {
  padding: 0 300px 40px 40px;
}
@media screen and (max-width: 738px) {
  .article-wrapper {
    padding: 0 20px 20px 20px ;
  }
}
.article-wrapper h4 {
  font-size: 1.5em;
  font-weight: 500;
}
.article-wrapper .title-wrapper {
  text-align: center;
}
.article-wrapper .main-ls {
  position: fixed;
  width: 233px;
  top: 104px;
  right: 20px;
  padding: 0 20px;
  border-left: 1px solid #d6d8da;
}
.article-wrapper .main-ls li {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article-wrapper .main-ls li:hover {
  background-color: #f0f2f5;
}
.article-wrapper .main-ls li:hover a {
  color: #1890ff;
}
.article-wrapper .controls {
  float: right;
}
.article-wrapper .controls .icon-tips {
  margin-right: 6px;
  vertical-align: middle;
  color: #6190e8;
  font-weight: bold;
}
.article-wrapper .controls .support-tip {
  margin-right: 20px;
  color: #6190e8;
}
.article-wrapper .side {
  position: fixed;
  top: 104px;
  width: 200px;
  right: 10px;
}
@media screen and (max-width: 738px) {
  .article-wrapper .side {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  }
}
