.comment-wrapper {
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
}
.comment-wrapper .comment-header {
  padding: 20px 0;
  color: #555;
  font-size: 16px;
}
.comment-wrapper .comment-header .count {
  color: #6190e8;
  border-bottom: 1px dotted #6190e8;
}
.comment-wrapper .comment-header .hr {
  margin: 10px 0 0 0;
}
.comment-wrapper .comment-header .menu-wrap {
  float: right;
  cursor: pointer;
}
.comment-wrapper .controls {
  float: right;
}
.comment-wrapper .controls .icon-tips {
  margin-right: 6px;
  vertical-align: middle;
  color: #6190e8;
  font-weight: bold;
}
.comment-wrapper .controls .support-tip {
  margin-right: 20px;
  color: #6190e8;
}
.comment-wrapper .reply-form {
  overflow: hidden;
}
.comment-wrapper .reply-form .reply-form-controls {
  float: right;
  padding: 10px 0;
}
.comment-wrapper .reply-form .reply-form-controls .tip {
  color: #c2c2c2;
  margin-right: 8px;
  font-size: 13px;
}
.comment-wrapper .icon-delete {
  color: #f00;
  vertical-align: middle;
  cursor: pointer;
  display: none;
}
.comment-wrapper .ant-comment-content:hover .icon-delete {
  display: inline-block;
}
.comment-wrapper .ant-popover-open .icon-delete {
  display: inline-block;
}
.comment-wrapper .icon-delete.active {
  display: inline-block;
}
.comment-wrapper .ant-comment-inner {
  padding: 6px 0;
}
.comment-wrapper .ant-comment-actions {
  margin-top: 0;
}
.comment-wrapper .ant-comment-content-detail p {
  white-space: inherit;
}
